const removeSpacesHelper = async (searchValue) => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search(searchValue);
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText("", "Replace");
    }
    await context.sync();
  });
};
const removeAllSpacesHelper = async (searchValue) => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search(searchValue, { matchWholeWord: false, ignoreSpace: false });
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText(" ", "Replace");
    }
    await context.sync();
  });
};

export const removeSpaces = async () => {
  for (const item of searchSettings.patterns.removeSpaces) {
    await removeSpacesHelper(item);
  }
};
export const removeAllSpaces = async () => {
  for (let index = 0; index < 5; index++) {
    await removeSpaces();
  }
  for (const item of searchSettings.patterns.removeAllSpaces) {
    await removeAllSpacesHelper(item);
  }
};

const addSpacesAfterHelper = async (searchValue) => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search(
      `[ |][${searchValue.slice(0, 1).toLowerCase()}|${searchValue.slice(0, 1).toUpperCase()}]${searchValue.slice(1)} `,
      { matchWildcards: true }
    );
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText(`${searchResults.items[i].text}`, "Replace");
    }
    await context.sync();
  });
};
export const addSpacesAfter = async () => {
  for (const item of searchSettings.patterns.spaceAfter) {
    await addSpacesAfterHelper(item);
  }
};
export const replaceHyperForNonBreakingHyper = async () => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search("\u002D", { matchWholeWord: false, ignoreSpace: true });
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText("\u2011", "Replace");
    }
    await context.sync();
  });
};
export const addSpacesAfterNames = async () => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search("[-]. [-]. ", { matchWildcards: true });
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText(
        searchResults.items[i].text.slice(0, 2) + "" + searchResults.items[i].text.slice(3, 5) + "",
        "Replace"
      );
    }
    await context.sync();
  });
};
export const addSpacesAfterNumber = async () => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search("[0-9] ", { matchWildcards: true });
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText(searchResults.items[i].text.slice(0, -1) + "", "Replace");
    }
    await context.sync();
  });
};

const removeExceptionSpacesAfterNumberHelper = async (searchValue) => {
  await Word.run(async (context) => {
    const searchResults = context.document.body.search("[0-9]" + searchValue, { matchWildcards: true });
    searchResults.load("font, text");
    await context.sync();
    for (let i = 0; i < searchResults.items.length; i++) {
      searchResults.items[i].insertText(
        searchResults.items[i].text.slice(0, 1) + " " + searchResults.items[i].text.slice(2),
        "Replace"
      );
    }
    await context.sync();
  });
};
export const removeExceptionSpacesAfterNumber = async () => {
  for (const item of searchSettings.patterns.removeExceptionSpacesAfterNumber) {
    await removeExceptionSpacesAfterNumberHelper(item);
  }
};
